import { XRPLWalletAccount } from '@xrpl-wallet-adapter/base';
import { XRPL_MAINNET } from '@xrpl-wallet-standard/core';

class CrossmarkWallet {
  #name = "Crossmark";
  #icon = "data:image/png;base64,";
  #accounts = [];
  #listeners = {};
  get version() {
    return "1.0.0";
  }
  get name() {
    return this.#name;
  }
  get icon() {
    return this.#icon;
  }
  get chains() {
    return [XRPL_MAINNET];
  }
  get features() {
    return {
      "standard:connect": {
        version: "1.0.0",
        connect: this.#connect
      },
      "standard:events": {
        version: "1.0.0",
        on: this.#on
      },
      "xrpl:signTransaction": {
        version: "1.0.0",
        signTransaction: this.#signTransaction
      },
      "xrpl:signAndSubmitTransaction": {
        version: "1.0.0",
        signAndSubmitTransaction: this.#signAndSubmitTransaction
      }
    };
  }
  get accounts() {
    return this.#accounts;
  }
  constructor() {
    if (new.target === CrossmarkWallet) {
      Object.freeze(this);
    }
  }
  #connect = async ({ silent = false } = {}) => {
    let address;
    if (silent) {
      address = window.xrpl.crossmark.session.address;
    } else {
      const { response } = await window.xrpl.crossmark.async.signInAndWait();
      address = response.data.address;
    }
    if (!address) return { accounts: [] };
    this.#accounts = [new XRPLWalletAccount(address)];
    this.#emit("change", { accounts: this.accounts });
    return {
      accounts: this.accounts
    };
  };
  #signTransaction = async ({ tx_json, account, network }) => {
    const { response } = await window.xrpl.crossmark.async.signAndWait(tx_json);
    return {
      signed_tx_blob: response.data.txBlob
    };
  };
  #signAndSubmitTransaction = async ({ tx_json, account, network }) => {
    const { response } = await window.xrpl.crossmark.async.signAndSubmitAndWait(tx_json);
    return {
      tx_json: response.data.resp.result,
      tx_hash: response.data.resp.result.hash
    };
  };
  #on = (event, listener) => {
    if (this.#listeners[event]) this.#listeners[event]?.push(listener);
    else this.#listeners[event] = [listener];
    return () => this.#off(event, listener);
  };
  #emit(event, ...args) {
    this.#listeners[event]?.forEach((listener) => listener.apply(null, args));
  }
  #off(event, listener) {
    this.#listeners[event] = this.#listeners[event]?.filter((existingListener) => listener !== existingListener);
  }
}

export { CrossmarkWallet };
